@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
  background-color: v.$secundario;
  padding: 5rem 0 0 0;

  &__grid {
    @include m.tablet {
      @include m.grid(3, 1.5rem);
    }
  }

  &__widget {
    margin-bottom: v.$separacion;
    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &__heading {
    color: v.$blanco;
    margin: 0;
    text-align: center;
    @include m.tablet {
      text-aling: left;
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
  }

  &__nav,
  &__texto {
    margin-top: 2rem;
  }

  &__link,
  &__texto {
    line-height: 1.7;
    color: v.$blanco;
    text-align: center;
    font-size: 2rem;
    @include m.tablet {
      text-align: center;
    }
  }

  &__copyright {
    background-color: v.$primario;
    margin: 0;
    color: v.$blanco;
    text-align: center;
    padding: 2rem;
  }
}
